.Map-container {
  margin: 2px 0px;
  overflow: hidden;

}



.park-div {
  float: left;
}

.map-div {
  float: right;
}

.btn {
  margin: 0px 1px;
}

.Parks-container {
  height: 600px ;
  overflow: scroll;
  
}

.Login-Form {
  margin: 100px auto;
  padding: 50px 50px;
  width: 25%;
}







@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}







@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width : 992px) {
  header, main, footer {
    padding-left: 0;
  }
}




